.followers {
    position: relative;
    z-index: 1000;
    flex-basis: calc(20%);
    height: 20  0px;
    padding: 0px 0px 20px 0px;
    border-radius: 5px;
    display: flex;
    flex-direction: column;
    align-items: center;
    gap: 20px;
}

.followers div {
    display: flex;
    justify-content: center;
    align-items: center;
}

.followers header{
    padding-top: 20px;
    display: flex;
    align-items: center;
    gap: 10px;
}

.followers header p{
    font-weight: 700;
}

.followers .main h1{
    font-size: 3.5em;
    font-weight: 700;
}

.followers .main{
    height: min-content;
    width: 100%;
    flex-direction: column;
    gap: 10px;
}

.followers .main p{
    font-size: 14px;
    font-weight: 400;
    letter-spacing: 5px;
}

.followers .changement {
    gap: 10px;
}

.followers .changement img{
    width: 8px;
    height: 4px;
}

.followers .changement p.positive{
    font-weight: 700;
    font-size: 0.7em;
    color: hsl(163, 72%, 41%);
}

.followers .changement p.negative{
    font-weight: 700;
    font-size: 0.7em;
    color: hsl(356, 69%, 56%);
}

.dark .followers {
    background-color: hsl(228, 28%, 20%); 
}

.dark .followers header p{
    color: hsl(228, 34%, 66%);
    font-size: 0.7em;
}

.dark .followers .main h1{
    color: hsl(0,100%,100%);
}

.dark .followers .main p{
    color: hsl(0,100%,100%);
}

.light .followers {
    background-color: hsl(227, 47%, 96%);
}

.light .followers header p{
    color: hsl(228, 12%, 44%);
    font-size: 0.7em;
}

.light .followers .main h1{
    color: hsl(230, 17%, 14%);
}

.light .followers .main p{
    color: hsl(230, 17%, 14%);
}


@media screen and (max-width : 1023px) {
    .followers {
        position: relative;
        z-index: 1000;
        width: 100%;
        height: 170px;
        padding: 0px 0px 20px 0px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 20px;
    }
    
    .followers div {
        display: flex;
        justify-content: center;
        align-items: center;
    }
    
    .followers header{
        display: flex;
        align-items: center;
        gap: 10px;
    }
    
    .followers header p{
        font-weight: 700;
    }
    
    .followers .main h1{
        font-size: 3.5em;
        font-weight: 700;
    }
    
    .followers .main{
        height: min-content;
        width: 100%;
        flex-direction: column;
        gap: 10px;
    }
    
    .followers .main p{
        font-size: 14px;
        font-weight: 400;
        letter-spacing: 5px;
    }
    
    .followers .changement {
        gap: 10px;
    }
    
    
}

@media screen and (min-width : 1024px) and (max-width : 1419px) {
    .followers {
        position: relative;
        z-index: 1000;
        flex-basis: 20%;
        height:  200px;
        padding: 0px 0px 20px 0px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 15px;
    }

    .followers header{
        display: flex;
        align-items: center;
        gap: 10px;
    }
    
    .followers header p{
        font-weight: 700;
    }
    
    .followers .main h1{
        font-size: 3em;
        font-weight: 700;
    }
    
    .followers .main{
        height: min-content;
        width: 100%;
        flex-direction: column;
        gap: 10px;
    }
    
    .followers .main p{
        font-size: 12px;
        font-weight: 400;
        letter-spacing: 5px;
    }
    
    .followers .changement {
        gap: 10px;
    }
}

.border {
    height: 4px;
    width: 100%;
}

#Facebook .border {
    background-color: hsl(208, 92%, 53%);
}

#Twitter .border {
    background-color: hsl(203, 89%, 53%);
}

#Instagram   .border {
    background: linear-gradient(to right, hsl(37, 97%, 70%), hsl(329, 70%, 58%));
}

#Youtube .border {
    background-color: hsl(348, 97%, 39%);
}

.padding{
    padding-top: 20px;

}